.container {
  padding: 20px;
  background-color: #f0f0f0;
  border-radius: 10px;
}

.table {
  margin-top: 20px;
}

.button-add {
  margin-bottom: 20px;
  background-color: #28a745;
  color: white;
}

.button-add:hover {
  background-color: #218838;
}

.dialog {
  min-width: 400px;
}

.snackbar {
  font-size: 18px;
}

.alert {
  display: flex;
  align-items: center;
}

.action-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

@media (max-width: 600px) {
  .action-buttons {
      flex-direction: column;
      align-items: center;
  }
}
